<template>
  <v-card id="quota-list">
    <!-- create -->
    <v-card-text>
      <div class="d-flex pb-2 ma-1">
        <v-btn
          color="primary"
          class="me-3"
          :to="{ name: 'apps-quota-add' }"
        >
          <v-icon
            size="18"
            class="me-1"
          >
            {{ mdiPlus }}
          </v-icon>
          <span>Create Quota</span>
        </v-btn>
        <br>

        <v-text-field
          v-model='search'
          single-line
          dense
          outlined
          hide-details
          placeholder="Search Quota"
          class="quota-list-search me-3 align-end"
        ></v-text-field>
        </div>
        </v-card-text>
    <!-- table -->

<template>
  <v-simple-table height="250">
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-uppercase">
            ID
          </th>
          <th class="text-center text-uppercase">
            Customer
          </th>
          <th class="text-center text-uppercase">
            Date Issued
          </th>
          <th class="text-center text-uppercase">
            Issued By
          </th>
          <th class="text-center text-uppercase">
            Status
          </th>
          <th class="text-center text-uppercase">
            Actions
          </th>
        </tr>
      </thead>

      <tbody v-if="quota.length">
        <tr
          v-for="item in search_result"
          :key="item.id"
        >

          <td>{{ item.id }}</td>
          <td>
          <div class="d-flex flex-column ms-4">
            <span class="d-block text--primary  font-weight-semibold text-truncate">{{ item.customer.first_name }} {{ item.customer.last_name }}</span>
            <span class="text-xs">{{ item.customer.email }}</span>
          </div>
          </td>
          <td class="text-center">
            {{ item.date_issued }}
          </td>
          <td class="text-center">
            {{ item.user }}
          </td>
          <td class="text-center">
            {{ item.status }}
          </td>
          <td class ="text-center">
            <button @click="editQuota(item.id)">
              <v-icon
                size="18"
                class="me-1"
              >
              {{ mdiPencilOutline }}
              </v-icon>
            </button>
            <button @click="deleteQuota(item.id)">
              <v-icon
                size="18"
                class="me-1"
              >
              {{ mdiDeleteOutline}}
              </v-icon>
            </button>
              <v-btn
                icon
                small

                :to="{ name: 'apps-quota-preview', params: { id: item.id } }"

              >
                <v-icon size="18">
                  {{ mdiEyeOutline }}
                </v-icon>
              </v-btn>



          </td>

        </tr>
      </tbody>
      <tbody v-else>
        <tr>
          <td colspan="7" class="text-center">
            No Quota Available
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
  </template>
  </v-card>
</template>

<script>

import {
  mdiPlus,
  mdiEyeOutline,
  mdiAccountCircleOutline,
  mdiDeleteOutline,
  mdiPencilOutline,
} from '@mdi/js';

import { onUnmounted, ref } from '@vue/composition-api';
import { avatarText } from '@core/utils/filter';
import store from '@/store';
import axios from "@axios";

export default{
  data() {
    return{
    quota:[],
    search:""

    }
  },
  mounted() {
    this.getQuota();
  },
  methods:{

    getQuota(){
      axios
        .get('/quota/')
        .then(response => {
        this.quota = response.data;

      })
      .catch(error=>{
        console.log(error.response)
      });

    },
    deleteQuota(id){
      axios
        .delete('/quota/'+id)
        .then(
        this.$router.push('/apps/quota/list')
        )
      .catch(error =>{
        console.log(error.response.data);
      });

    },
    editQuota(id){
      this.$router.push({ name: 'apps-quota-edit', params: { id: id } });
    },
  },
  computed:{
    search_result(){
      if(this.search){
        return this.quota.filter(search_item=>{
          return search_item.customer.first_name.toLowerCase().includes(this.search.toLowerCase())
        })
      }
      else{
        return this.quota
      }
    }


  },
  setup(){
    return{
      mdiPlus,
      mdiEyeOutline,
      mdiPencilOutline,
      mdiDeleteOutline,
      avatarText,
      store,
    }
  }
}


</script>

<style lang="scss" scoped>
#quota-list {
  .quota-list-search {
    max-width: 10.625rem;
  }
}
</style>
